.TodoCounter {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 300px;
  height: 100px;
  margin: 0;
  text-align: center;
  font-size: clamp(28px, 2.8vw, 30px);
  background-color: #303134;
  border-bottom: 1px solid #56585a;
  color: #f5f5f5;
}

